import GNode from './GNode';
import GShapePolygon from './GShapePolygon';


export default class GShapeDiamond extends GShapePolygon
{
	public paddingEm = 1.5;

	protected getSvgPolygonPoints(node: GNode): number[][]
	{
		let s = node.getSize();
		let w = s.width / 2;
		let h = s.height / 2;

		return [
			[ 0, -h ],
			[ w,  0 ],
			[ 0, +h ],
			[ -w, 0 ],
		];
	}

}

