import GNode from './GNode';
import GShapeRect from './GShapeRect';
import GSize from "./GSize";
import GSvg from './GSvg';


export default class GShapeNestedGraph extends GShapeRect
{

	public renderElements(svg: GSvg, node: GNode): any[]
	{
		let s = node.getSize();
		let w = s.width;
		let h = s.height;

		return [
			svg.rect({
				'x': - w / 2,
				'y': - h / 2,
				'width': w,
				'height': h,
				'stroke-width': this.strokeWidth,
				'stroke': node.attr('color'),
				'fill': node.attr('fill'),
			}),
			this.renderLabel(svg, node),
		];
	}


	public getMinSize(node: GNode): GSize
	{
		if (node.hasNestedGraph()) {
			return node.getNestedGraph().getBoundingBox().addMargin(3 * this.getPadding(node))
				.getSize().fit(super.getMinSize(node));
		} else {
			return super.getMinSize(node);
		}
	}


}

