import GNode from './GNode';
import GShapeRect from './GShapeRect';
import GSvg from './GSvg';


export default class GShapeLabel extends GShapeRect
{

	public renderElements(svg: GSvg, node: GNode): any[]
	{
		return [
			this.renderLabel(svg, node),
		];
	}

	public getPadding(node: GNode): number
	{
		return 0;
	}

}

