import GShape from './GShape';
import GShapeLibrary from './GShapeLibrary';
import GShapeRect from './GShapeRect';

export default class GFallbackShapeLibrary extends GShapeLibrary
{

	public listShapes(): string[]
	{
		return [];
	}


	protected createShape(name: string): GShape
	{
		console.log('Fallback shape library: creating shape ' + name);
		return new GShapeRect();
	}

}

