import GNode from './GNode';
import GShape from './GShape';
import GSvg from './GSvg';


export default class GShapeEllipse extends GShape
{

	public renderElements(svg: GSvg, node: GNode): any[]
	{
		let s = node.getSize();
		return [
			svg.ellipse({
				'cx': 0,
				'cy': 0,
				'rx': s.width / 2,
				'ry': s.height / 2,
				'stroke-width': this.strokeWidth,
				'stroke': node.attr('color'),
				'fill': node.attr('fill'),
			}),
			this.renderLabel(svg, node),
		];
	}


	protected getSvgIntersectionsShapes(node: GNode): any[]
	{
		let s = node.getSize();
		return [
			[ "ellipse", { cx: 0, cy: 0, rx: s.width / 2, ry: s.height / 2 } ],
		];
	}

}

